import React from 'react';
import { useState } from 'react';

function Contact() {
  const [inputValue, setInputValue] = useState({
    name: '',
    email: '',
    password: '',
    msg: ''
  });

  const inputEvent = (e) =>{
    // alert(e.target.value);
    const {name,value} = e.target;
    setInputValue((prevData) =>{
      return {
        ...prevData,
      [name] : value,
      }
    });
  }

  const formSubmit = (e) =>{
    e.preventDefault();
    alert(`My name is ${inputValue.name}<br/>My email is ${inputValue.email}<br/>My password is ${inputValue.password}<br/>My message is ${inputValue.msg}`);
    setInputValue({
      name: '',
      email: '',
      password: '',
      msg: ''
    });
  }

  return (
    <>
      <div className="my-5 text-center contact_heading">
        <h1>Contact Us</h1>
      </div>

      <div className="container contact_div">
        <div className="row">
          <div className="col-md-6 col-10 mx-auto shadow p-5 mb-5">
            <p>We always looking to explore new ideas and help create good things. Let's chat!</p>
            <form onSubmit={formSubmit} className="mt-5">
              <div className="form-group">
                <label for="exampleInputEmail1">Name</label>
                <input name="name" value={inputValue.name} onChange={inputEvent} type="text" className="form-control" id="exampleInputName" aria-describedby="emailHelp" />
              </div>
              <div className="form-group">
                <label for="exampleInputEmail1">Email address</label>
                <input name="email" value={inputValue.email} onChange={inputEvent} type="email" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" />
              </div>
              <div className="form-group">
                <label for="exampleInputPassword1">Password</label>
                <input name="password" value={inputValue.password} onChange={inputEvent} type="password" className="form-control" id="exampleInputPassword1" />
              </div>
              <div className="form-group">
                <label for="exampleFormControlTextarea1">Message</label>
                <textarea name="msg" value={inputValue.msg} onChange={inputEvent} className="form-control" id="exampleFormControlTextarea1" rows="3"></textarea>
              </div>
              <div className="mt-3 text-center">
              <button type="submit" className="btn-get-started">Submit</button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </>
  );
}

export default Contact;
