import React from 'react';
import aboutImg from './images/about-us.png';
import {NavLink} from 'react-router-dom';

function About() {
  return (
    <>
      <section id="main_header" className="d-flex align-items-center">
        <div className="container">
          {/* <div className="row">
            <div className="col-10 mx-auto"> */}
              <div className="row">
                <div className="col-6 pt-5 pt-lg-0 order-2 order-lg-1 d-flex justify-content-center flex-column">
                  <h1>About Us</h1>
                  <p className="mt-3">Lorem ipsum dolor sit, amet consectetur adipisicing elit. Eaque maxime repudiandae quae reiciendis quidem mollitia nihil aperiam! Itaque asperiores suscipit dolorem, in minima sunt voluptatum natus maxime minus qui unde!</p>
                  <div className="mt-3">
                    <NavLink to="/contact" className="btn-get-started">Contact</NavLink>
                  </div>
                </div>
                <div className="col-6 order-1 order-lg-2">
                  <img src={aboutImg} alt="header_img" className="img-fluid animate" />
                </div>
              </div>
            {/* </div>
          </div> */}
        </div>
      </section>
    </>
  );
}

export default About;
