import React from 'react';
import web from './images/hh.png';
import { NavLink } from 'react-router-dom';


function Home() {
  return (
    <>
      <section id="main_header" className="d-flex align-items-center">
        <div className="container">
          {/* <div className="row">
            <div className="col-10 mx-auto"> */}
              <div className="row">
                <div className="col-6 pt-5 pt-lg-0 order-2 order-lg-1 d-flex justify-content-center flex-column">
                  <h1>Grow your business with <strong className="brand_name">John Khan.</strong></h1>
                  <h2>We are team of talented boys!</h2>
                  <div className="mt-3">
                    <NavLink to="/services" className="btn-get-started">Get Started</NavLink>            
                  </div>
                </div>
                <div className="col-6 order-1 order-lg-2 header_img">
                  <img src={web} alt="header_img" className="img-fluid animate"/>
                </div>
              </div>
            {/* </div>
          </div> */}
        </div>
      </section>
    </>
  );
}

export default Home;
